let imageQueue = [];
const getHashTagParam = (rID) => {

    const parsed = queryString.parse(window.location.search);
    const location = window.location.href;

    var header_tags = {};
    var updateContainerID = rID ? `#co_${rID}` : ".taggbox"

    if (location.includes('widget.socialwalls') || parsed["social-wall"] || location.includes('social-wall')) {

        let updateDataTags = { tag: 1 }
        let getDisplayWall = { getDisplayWall: 1 }
        header_tags = { ...getDisplayWall, ...updateDataTags };

    } else if (document.querySelector(updateContainerID) != null) {
        const taggbox = document.querySelector(updateContainerID);
        if (taggbox) {
            if (taggbox.getAttribute('data-social-wall')) {
                let updateDataTags = { tag: 1 }
                let getDisplayWall = { getDisplayWall: 1 }
                header_tags = { ...getDisplayWall, ...updateDataTags };
            }
        }
    }


    if (parsed.tags) {
        const tags = parsed.tags;
        const updateTags = { tag: tags.includes("true") ? 1 : 0 }
        let updateDataKeywords = {}
        if (parsed["data-keywords"]) {
            updateDataKeywords = { keywords: `${String(decodeURIComponent(parsed["data-keywords"]))}` }
        }
        header_tags = { ...updateTags, ...updateDataKeywords };
    }
    else if (document.querySelector(updateContainerID) != null) {
        // const taggbox = document.querySelector(updateContainerID);
        // if (taggbox) {
        //     if (taggbox.getAttribute('data-tags')) {
        //         const dataTags = taggbox.getAttribute('data-tags');
        //         let updateDataTags = { tag: dataTags.includes("true") ? 1 : 0 }

        //         const dataKeywords = taggbox.getAttribute('data-keywords');
        //         let updateDataKeywords = {}
        //         if (dataKeywords) {
        //             updateDataKeywords = { keywords: `${String(decodeURIComponent(dataKeywords))}` }
        //         }
        //         header_tags = { ...updateDataTags, ...updateDataKeywords };
        //     }
        // }
    }

    return { headers: header_tags }
}
const getThemeId = (ID) => {

    var themeId = "";
    var updateContainerID = ID ? `#co_${ID}` : ".taggbox"
    var themeUrl = window.location.href.includes("theme-id");

    if (document.querySelector(updateContainerID) != null) {
        const taggbox = document.querySelector(updateContainerID);

        if (taggbox) {
            if (taggbox.getAttribute('theme-id')) {
                themeId = taggbox.getAttribute('theme-id');
            }
        }
    } else if (themeUrl) {
        themeId = getParameter('theme-id');
    }

    return themeId
}
export const themeByWallID = (Id, heightEvent) => {
    return (dispatch) => {

        try {
            var isEmbedLoad = window.location.href.includes("editor=1") ? false : window.editor ? false : true
            if (isEmbedLoad) urlTracking(Id);
            dispatch({ type: RENDER_ID, payload: rID })
            const themeId = getThemeId(rID);
            if (Id == undefined) window.location.href = "https://socialwalls.com/";
            new HttpClient()
                .get(`${GET_WIDGET_DATA_API_PATH}${Id}`, getHashTagParam(rID))
                .then((response) => {
                    if (parseInt(response.data.response_code) == 404) {
                        dispatch({
                            type: ERROR_RESPONSE_WITH_MESSAGE,
                            payload: response.data,
                        });
                        throw new Error(response.data.message);
                    }
                    else if (suiteTrack || window.editor || response.data.visitorLimit === 0) {
                        const postLists = response.data['postData'];
                        if (window.location.href.includes("wall/personalize")) {
                            response.data['postData'] = postLists.slice(0, 25);
                        }

                        response.data['wall']["isDisplay"] = false;
                        response.data['wall']["isEditor"] = false;
                        if ([74, 75, 76, 77].includes(response.data.wall.ThemeRule.themeId)) {
                            response.data['wall']['ThemeRule'] = { ...response.data.wall.ThemeRule, ...theme62Setting }
                            response.data['wall']['Personalization']['trimcontent'] = 1;
                            response.data['wall']['Personalization']['loadMoreStatus'] = 1
                        }

                        dispatch({ type: GET_APP_DATA, payload: response.data, heightEvent: heightEvent, themeId: themeId });
                        dispatch({ type: ONSITE_TOKEN_UPDATE, payload: response.data.onsite_token });

                    } else {

                        dispatch({
                            type: ERROR_RESPONSE_WITH_MESSAGE,
                            payload: { message: "", type: "VISITOR_LIMIT_EXCEEDED", ...response.data },
                        });
                        throw new Error("");
                    }
                    // dispatch({
                    //     type: GET_THEME_ERROR_SUCCESS,
                    //     payload: {
                    //         error_code: error_code,
                    //         post_message: post_message,
                    //         older_days: older_days,
                    //         older_post: older_post,
                    //     },
                    // });

                })
                .catch((error) => {
                    const { response } = error;
                    if (response && response.status && response.status == 404) {
                        if (parseInt(response.status) == 404) {
                            dispatch({
                                type: ERROR_RESPONSE_WITH_MESSAGE,
                                payload: response.data,
                            });
                        }
                    }
                    dispatch({ type: GET_LOADER_ERROR });
                });
        }
        catch (ex) {
            const { response } = ex;
            dispatch({
                type: ERROR_RESPONSE_WITH_MESSAGE,
                payload: { message: "", type: "VISITOR_LIMIT_EXCEEDED", ...response.data },
            });
            throw new Error("");
        }
    };
};

