export const getDataNextSteps = (
    
    wallID,
    timeStamp,
    postCount,
    networkId,
    after,
    heightEvent,
    upatemoreData
) => async (dispatch) => {

        postCount = getPersonalize() ? 5 : postCount;
        const urlToAccess = `${GET_DATA_POST_APPEND}${wallID}/${timeStamp}/${postCount}/${networkId}/1/${after}`;
        const { wall, postData } = store.getState().appData;
        const afterUsedList = postData.afterUsedList;
        const bestFit = (wall.ThemeRule.themeId === 4 && wall.Personalization.fitRow == 1);
        dispatch({ type: GET_THEME_SHOW_MORE_LOADER_SUCCESS, payload: true });
        const { isFilterLoading } = store.getState().loaderData;
        var updateHeader = getHashTagParam(rID)
        if (updateHeader && updateHeader.headers && Object.keys(updateHeader.headers).length > 0) {
            if (updateHeader.headers.tag) {
                updateHeader.headers.tag = 1;
            }
            else {
                if (postData.appendData.tagName) updateHeader.headers.tag = 1;
            }
            if (updateHeader.headers.keywords) {
                if (updateHeader.headers.keywords.length > 0) updateHeader.headers.keywords = `${updateHeader.headers.keywords}`
                else updateHeader.headers.keywords = `${updateHeader.headers.keywords}`
            }
            else {
                if (postData.appendData.tagName) updateHeader.headers.keywords = postData.appendData.tagName;
            }
            updateHeader.headers.action = 2;
        }
        else {
            if (postData.appendData.tagName) {
                var headerData = { tag: 1, keywords: `${postData.appendData.tagName}`, action: 2 }
                updateHeader.headers = headerData;
            }
            else {
                var headerData = { action: 2 }
                updateHeader.headers = headerData;
            }
        }

        if ((!afterUsedList.includes(after) || afterUsedList.length == 0) && !isFilterLoading) {

            themePostTracking({
                action: 1,
                wall: wallID,
                feed: "",
                post: "",
                owner: wall.Wall.owner
            });
            gtmAnalyticsAction("Tagbox", "showmore_click", "showmore", wallID)
            await new HttpClient()
                .get(urlToAccess, updateHeader)
                .then((response) => {

                    

                    dispatch({
                        type: SHOW_MORE_DATA_UPDATE, payload: {
                            newData: response.data.data, filterNetworkId: networkId, appendData: {
                                after: response.data.after,
                                networkID: networkId,
                                heightEvent: heightEvent,
                                tagName: postData.appendData.tagName
                            },
                            tagName: postData.appendData.tagName,
                            hasMoreData: {
                                [networkId]: {
                                    hasMoreData: bestFit && Object.keys(response.data.data).length < postCount ? false : response.data.hasPost == 1 ? true : false,
                                    after: response.data.after,
                                },
                            }
                        }
                    });
                    dispatch({
                        type: GET_THEME_SHOW_MORE_LOADER_SUCCESS,
                        payload: false,
                    });
                })
                .catch((error) => { });
          }

    if(wall.product && upatemoreData && (typeof upatemoreData === 'function')) upatemoreData();
};
