const eventOddVal = (w) => w % 2 == 0 ? 'even' : 'odd';

export const getTotalRowsForGalleryTheme = (postData, ColumnCount) => {
    var newPostdata = [], i = 0, n = postData.length, j = 0, devide;
    var secondArr = ColumnCount == 5 ? 7 : ColumnCount == 4 ? 5 : ColumnCount == 3 ? 3 : ColumnCount == 2 ? 1 : 5;
    while (i < n) {
        devide = j % 2 == 0 ? ColumnCount : secondArr;
        newPostdata.push(postData.slice(i, i += devide));
        j++;

    }
    return newPostdata;
}

export const getTotalRows = (postData, ColumnCount) => {
    var newPostdata = [],
        i = 0,
        n = postData.length;

    while (i < n) {
        newPostdata.push(postData.slice(i, i += ColumnCount))
    }
    return newPostdata;
}

export const brandingUrl = (id, wallId) => {
    let anchorText = 'Website Widget';
    let socialWallURL = false
    switch (Array.isArray(id) && id != '' && id.length === 1 ? id[0] : 0) {
        case 1:
            if (eventOddVal(wallId) == 'even') anchorText = 'social wall'
            else anchorText = 'social media wall'
            return ['https://socialwalls.com/', anchorText]
        case 2:
        case 18:
            if (eventOddVal(wallId) == 'even') {
                anchorText = 'Instagram Widget'
            } else {
                socialWallURL = eventOddVal(Math.floor((wallId / 10) % 10)) == 'even' ? false : true
                anchorText = eventOddVal(Math.floor((wallId / 10) % 10)) == 'even' ? 'Best Instagram Widget' : 'social wall'
            }
            return [socialWallURL ? 'https://socialwalls.com/' : 'https://taggbox.com/instagram-widget/', anchorText]
        case 3:
            if (eventOddVal(wallId) == 'even') {
                anchorText = 'user generated content'//'Facebook Review Widget'
            } else {
                anchorText = 'UGC'//'Best Facebook Review Widget'
            }
            return ['https://taggbox.com/blog/user-generated-content/', anchorText]
        case 4:
            if (eventOddVal(wallId) == 'even') {
                anchorText = 'Google Review Widget'
            } else {
                anchorText = 'Best Google Review Widget'
            }
            return ['https://taggbox.com/google-reviews-widget/', anchorText]
        case 7:
            return ['https://taggbox.com/blog/user-generated-content/', 'user generated content']
        case 28:
            return ['https://taggbox.com/tiktok-widget/', 'Tiktok Widget']
        case 10:
            return ['https://taggbox.com/blog/user-generated-content/', 'user generated content']
        default:
            return ['https://taggbox.com/widget/', anchorText]
    }
}

