export const timeAgo = (timestamp) => {
    // Convert the Unix timestamp to milliseconds
    const currentTime = Date.now();
    const targetTime = timestamp * 1000;

    // Calculate the difference in milliseconds
    const difference = currentTime - targetTime;

    // Define time intervals in milliseconds
    const minute = 60 * 1000;
    const hour = minute * 60;
    const day = hour * 24;
    const week = day * 7;
    const month = day * 30;
    const year = day * 365;

    // Determine the appropriate time unit to display
    if (difference < minute) {
        return 'just now';
    } else if (difference < hour) {
        const minutes = Math.floor(difference / minute);
        return `${minutes} minute${minutes > 1 ? 's' : ''} ago`;
    } else if (difference < day) {
        const hours = Math.floor(difference / hour);
        return `${hours} hour${hours > 1 ? 's' : ''} ago`;
    } else if (difference < week) {
        const days = Math.floor(difference / day);
        return `${days} day${days > 1 ? 's' : ''} ago`;
    } else if (difference < month) {
        const weeks = Math.floor(difference / week);
        return `${weeks} week${weeks > 1 ? 's' : ''} ago`;
    } else if (difference < year) {
        const months = Math.round(difference / month);
        return `${months} month${months > 1 ? 's' : ''} ago`;
    } else {
        const years = Math.floor(difference / year);
        return `${years} year${years > 1 ? 's' : ''} ago`;
    }
}
var postDetail = []
var postId = []
var listenerAdded = false
export const themeImpressionTracking = (ref) => {
    const { wall } = store.getState().appData
    if (!listenerAdded) {
        window.addEventListener('beforeunload', (event) => {
            const headers = new Headers({
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${AUTHORIZATION_TOKEN}`,
                'Productdomain': 'taggbox.com'
            });

            if (postDetail.length > 0) {
                fetch(UPDATE_SHOW_MORE_AND_POST_TRACKING, {
                    method: 'POST',
                    headers: headers,
                    body: JSON.stringify({
                        "action": 2,
                        "wall": wall.Wall.id,
                        "posts": postDetail,
                        "owner": wall.Wall.owner
                    }),
                    keepalive: true
                });
            }
        })
        listenerAdded = true
    }
    let observer = new IntersectionObserver(entries => {
        entries.forEach(entry => {
            if (entry.isIntersecting && !postId.includes(entry.target.getAttribute("post-id"))) {
                postDetail.push({ "post": entry.target.getAttribute("post-id"), "feed": entry.target.getAttribute("feed-id") })
                postId.push(entry.target.getAttribute("post-id"))
                observer.unobserve(ref.current)
            }
        });
    });
    if (ref.current) { observer.observe(ref.current); }
}


export const isAppleSafari = () => {
    const appleExpression = /Apple/i;
    const safariExpression = /Safari/i;
    return appleExpression.test(navigator.vendor) &&
        safariExpression.test(navigator.userAgent);
};

