export const checkMobileView = () => {
    const isTablet = window.window.matchMedia("(max-width: 1200px)");
    const isMobile = window.matchMedia("(max-width: 767px)");
    if (isMobile.matches) return 1;
    else if (isTablet.matches) return 2;

}

// function to check if the chrome browser is previous version (< 100);
export const isOldBrowser = () =>{
    try {
        let oldBrower = false;
        if(window.navigator){
            const agentDetails = window.navigator.userAgent.split(' ');
            if(agentDetails && agentDetails.length > 0){
                if(agentDetails.includes("iPhone")) return true;
                const isChorme = agentDetails.filter(agent => agent.includes('Chrome'));
                if(isChorme && isChorme.length > 0){
                    const versionDetails = isChorme[0].split('/');
                    if(versionDetails && versionDetails.length > 1){
                        const versions = versionDetails[1];
                        if(versions && versions.length > 0){
                            let version = versions.split('.');
                            if(version && version.length > 0){
                                let majorV = parseInt(version[0]);
                                return !isNaN(majorV) ? majorV < 100 : false;
                            }
                        }
                    }
                }
            }
        }
        return oldBrower;
    } catch (error) {
        return false;
    }
}