import { GET_APP_DATA,GET_APP_DATA_NEW, SHOW_MORE_DATA_UPDATE, FILTER_DATA_UPDATE, ERROR_RESPONSE_WITH_MESSAGE, GET_THEME_ERROR_SUCCESS, PLAN_LIMIT, GET_APP_DATA_THEME, UPDATE_PRODUCT } from './reducersKeys'
import { ERROR, POST_DATA, LOADER, WALL_ID, LANGUAGE_SETTINGS, WALL_DATA, WEB_FILTERS, NETWORK_DATA, FILTER_TAGS, ANNOUNCEMENTS_DATA } from './contants'
import { languageSettings } from '../actions/themeActions'
import { convertObjectToArray, convertObjectToArray_Tags, convertObjectToArray_value, isDisplayActive } from '../utils'
import { SHARE_ROOT_URL, suiteTrack } from "../actions/api";
const initialState = {
    error: ERROR,
    postData: POST_DATA,
    loader: LOADER,
    wall: WALL_DATA,
    wallID: WALL_ID,
    languageSetting: LANGUAGE_SETTINGS,
    webFilters: WEB_FILTERS,
    networkData: NETWORK_DATA,
    filter_tags: FILTER_TAGS,
    feeds: null,
    product : null,
    announcements: ANNOUNCEMENTS_DATA
}
export default function reducer(state = initialState, action) {
 
    switch (action.type) {

        case UPDATE_PRODUCT: {
             return { ...state, ...state.wall.isDisplay = action.payload }
        }
        case GET_APP_DATA: {
            const widgetTheme = action.themeId ? action.payload.wall.ThemeRule.themeId = parseInt(action.themeId) : '';
            const loadData = APP_DATA(action.payload, action.heightEvent, widgetTheme)
             return { ...state, ...loadData }
        }
        case GET_APP_DATA_NEW: {

             const completeDataObject = action.payload.completeDataObject;
             const postData = action.payload.postData;
             return {  ...state, completeDataObject : completeDataObject, ...state.postData.completeData = postData, ...state.postData.postData = postData }; 
         }
        
        case GET_APP_DATA_THEME: {
            // const widgetTheme = action.themeId ? action.payload.wall.Personalization.widgetTheme = parseInt(action.themeId) : '';
             //const loadData = APP_DATA(action.payload, action.heightEvent, widgetTheme)
            return { ...state, wall: { ...state.wall, ...action.payload } }
        }

        case SHOW_MORE_DATA_UPDATE: {
            const { newData, filterNetworkId, appendData, hasMoreData, tagName } = action.payload;
            var PostUpdateData = postDataRenderData(filterNetworkId, state.postData, newData, tagName, state.filter_tags)
            PostUpdateData.afterUsedList = [...PostUpdateData.afterUsedList, PostUpdateData.appendData.after]
            PostUpdateData.hasMoreData = { ...PostUpdateData.hasMoreData, ...hasMoreData }

            let appendNewData = {
                after: appendData.after,
                networkID: appendData.networkID,
                heightEvent: appendData.heightEvent
                    ? appendData.heightEvent
                    : PostUpdateData.appendData.heightEvent,
                tagName: tagName
            };
            PostUpdateData.appendData = appendNewData;

            return {
                ...state,
                postData: PostUpdateData
            };
        }
        case FILTER_DATA_UPDATE: {
            const { newData, filterNetworkId, appendData, hasMoreData, tagName } = action.payload;

            var PostUpdateData = postDataRenderData(filterNetworkId, state.postData, newData, tagName, state.filter_tags)
            PostUpdateData.afterUsedList = [...PostUpdateData.afterUsedList, PostUpdateData.appendData.after]
            PostUpdateData.hasMoreData = { ...PostUpdateData.hasMoreData, ...hasMoreData }
            let appendNewData = {
                after: appendData.after,
                networkID: appendData.networkID,
                heightEvent: appendData.heightEvent
                    ? appendData.heightEvent
                    : PostUpdateData.appendData.heightEvent,
                tagName: tagName
            };
            PostUpdateData.appendData = appendNewData;
            return {
                ...state,
                postData: PostUpdateData
            };

        }
        case ERROR_RESPONSE_WITH_MESSAGE: {
            var errorData = state.error;
            errorData.errorWithMessage = action.payload;

            return { ...state, error: errorData }
        }
        case GET_THEME_ERROR_SUCCESS: {
            var errorData = state.error;
            errorData.themeError = action.payload;
            return { ...state, error: errorData }
        }
        case PLAN_LIMIT: {
            var errorData = state.error;
            errorData.planLimit = action.payload;
            return { ...state, error: errorData }
        }
        default:
            return { ...state }
    }

}


const APP_DATA = (response, heightEvent) => {
    let Error = ERROR;
    var PostUpdateData = POST_DATA;
    let LoaderData = LOADER;
    let WallData = WALL_DATA;
    let WallID = WALL_ID;
    let LanguageData = LANGUAGE_SETTINGS;
    let WebFilter = WEB_FILTERS;
    let filter_tag = FILTER_TAGS;
    let AnnouncementData = ANNOUNCEMENTS_DATA
    const isDisplay = isDisplayActive();

    const {
        feeds,
        planLimit,
        after,
        customPost,
        postData,
        webFilters,
        error_code,
        post_message,
        older_days,
        older_post,
        wall,
        product,
        loader_type, loader_color1, loader_color2, filter_tags, announcements
    } = response
 
    Error.planLimit = planLimit ? planLimit : false;

    if (wall && wall.UserRule && wall.UserRule.branding && wall.UserRule.branding === 1 && ![55].includes(wall.ThemeRule.themeId)) PostUpdateData.isFreeAdsStatus = true;
    // else {
    //     if (wall && wall.UserDetail && wall.UserDetail.planId && ["1", "53"].includes(wall.UserDetail.planId) && ![55].includes(wall.Personalization.widgetTheme)) PostUpdateData.isFreeAdsStatus = true;
    // }
    LoaderData.loader = { loader_type, loader_color1, loader_color2, wall };
    LoaderData.isLoaderEnable = loader_type === 404 ? false : true;

    if(isDisplay){
        wall['ThemeRule']['themeId'] = wall.Personalization.signageTheme;
    }else{
        wall['ThemeRule']['themeId'] = wall.displayWall ? wall.ThemeRule.themeId : wall.Personalization.widgetTheme;
    }
    WallData = wall;
    WallID = wall.Wall.id;
    AnnouncementData = announcements


    PostUpdateData.wallId = wall.Wall.id
    PostUpdateData.userId = wall.Wall.owner
    PostUpdateData.db_table = wall.UserDetail.db_table

    LanguageData = {
        ...languageSettings(
            wall.Personalization.w_language,
            wall.Personalization.custom_lan_data
        )
    }
    Error.themeError = {
        error_code: error_code,
        post_message: post_message,
        older_days: older_days,
        older_post: older_post,
    }
    filter_tag = filter_tags ? convertObjectToArray_Tags(filter_tags) : [];
    PostUpdateData.customPostData = customPost;
    PostUpdateData = postDataRenderData(PostUpdateData.filterNetworkId, PostUpdateData, postData, PostUpdateData.tagName, filter_tag)
    WebFilter = webFilters ? convertObjectToArray(webFilters) : [];

    PostUpdateData.appendData = {
        after: after,
        networkID: 0,
        heightEvent: heightEvent
            ? heightEvent
            : PostUpdateData.appendData.heightEvent,
    }


    return {
        error: Error,
        postData: PostUpdateData,
        loader: LoaderData,
        wall: WallData,
        wallID: WallID,
        languageSetting: LanguageData,
        webFilters: WebFilter,
        filter_tags: filter_tag,
        feeds: feeds,
        announcements: AnnouncementData,
        product : product
    }
}


var postDataRenderData = (filterNetworkId, PostUpdateData, newData, tagName, filter_tags) => {

    let filterId = filterNetworkId
        ? filterNetworkId
        : PostUpdateData.currentFilterNetworkId;
    if (!filterId) {
        filterId = 0;
    }

    let completeDataObject = PostUpdateData.completeDataObject;
    let filteredDataObject = PostUpdateData.filteredDataObject;
    let tagfilteredDataObject = PostUpdateData.tagfilteredDataObject;
    let completeData = PostUpdateData.completeData;
    let customPostData = PostUpdateData.customPostData ? PostUpdateData.customPostData : {};
    let freeAdsPosition = PostUpdateData.freeAdsPosition;


    if (Object.keys(newData).length) {
        Object.keys(newData).forEach((key) => {
            const item = newData[key];

            if (!completeDataObject[item.id]) {

                var refererUrl = PostUpdateData.originUrl;
                if (refererUrl && String(refererUrl).includes("widget/wall/index")) {
                    refererUrl = `https://widget.taggbox.com/${PostUpdateData.wallId}`
                }
                const description = encodeURIComponent(
                    item.content + (PostUpdateData.userId !== 30879 ? " via #taggbox" : '')
                );
                // item.content + 
                const shareUrl = encodeURIComponent(
                    SHARE_ROOT_URL +
                    window.btoa(
                        item.id + "---" + PostUpdateData.wallId + "---" + refererUrl + "---" + PostUpdateData.db_table + `${suiteTrack ? `---ugc-suite` : ``}`
                    )
                );

                const facebookShareUrl = (PostUpdateData.userId == 146945 && item.share && item.share.facebook) ? item.share.facebook : `https://www.facebook.com/sharer.php?u=${shareUrl}`;
                let twitterShareUrl = `https://twitter.com/intent/tweet?text=${description}`;
                if (item.type !== 1) {
                    twitterShareUrl = twitterShareUrl + "&url=" + shareUrl;
                }
                const plusGoogleShareUrl = `https://plus.google.com/share?url=${shareUrl}`;
                const linkedinShareUrl = `https://www.linkedin.com/shareArticle?mini=true&url=${shareUrl}`;
                item.shareDetail = {
                    shareUrl: SHARE_ROOT_URL +
                        window.btoa(
                            item.id + "---" + PostUpdateData.wallId + "---" + refererUrl + "---" + PostUpdateData.db_table
                        ), description: (PostUpdateData.userId !== 30879 ? " via #taggbox" : '')
                }

                item.share = {
                    ...item.share,
                    facebook: facebookShareUrl,
                    twitter: twitterShareUrl,
                    linkedin: linkedinShareUrl,
                    google: plusGoogleShareUrl
                };
                
                completeDataObject[item.id] = item;
            }
            if (filteredDataObject[item.network.id]) {
                if (!filteredDataObject[item.network.id].includes(item.id)) {
                    filteredDataObject[item.network.id].push(item.id);
                }
            } else {
                filteredDataObject[item.network.id] = [item.id];
            }
            if (filter_tags && filter_tags.length > 0) {
                if (item.tags && String(item.tags) && item.tags.length > 0) {
                    let updatedFilterTag = String(item.tags).toLocaleLowerCase()
                    filter_tags.map((filterTagName) => {
                        if (updatedFilterTag.includes(String(filterTagName.tagName).toLocaleLowerCase())) {
                            if (tagfilteredDataObject[String(filterTagName.tagName).toLocaleLowerCase()]) {
                                //if(isPreRenderData){
                                if (!tagfilteredDataObject[String(filterTagName.tagName).toLocaleLowerCase()].includes(item.id)) {
                                    tagfilteredDataObject[String(filterTagName.tagName).toLocaleLowerCase()].push(item.id);
                                }
                            } else {
                                tagfilteredDataObject[String(filterTagName.tagName).toLocaleLowerCase()] = [item.id];
                            }
                        }
                    })
                }
            }

            if (!completeData.includes(item.id)) completeData.push(item.id);

            initialState.networkData[key] = { networkId: item.network.id, networkName: item.network.name }
        });
    }

    let postData = filterId && filteredDataObject[filterId]
        ? filteredDataObject[filterId]
        : filterId === 0
            ? completeData
            : [];

    postData = tagName && tagName.length > 0 && tagfilteredDataObject[String(tagName).toLocaleLowerCase()] ? tagfilteredDataObject[String(tagName).toLocaleLowerCase()] : postData;


    if (customPostData && Object.keys(customPostData).length) {
        Object.keys(customPostData).forEach((key) => {
            let item = customPostData[key];
            const { status, after, every } = item;
            if (status === 1 && every != 0) {
                let customPostIndex = parseInt(after);
                for (
                    customPostIndex;
                    customPostIndex <= completeData.length;
                    customPostIndex = customPostIndex + parseInt(every)
                ) {
                    if (completeData.length > customPostIndex) {
                        if (completeData[customPostIndex] !== item.id)
                            completeData.splice(customPostIndex, 0, item.id);
                        if (!completeDataObject[item.id]) {
                            completeDataObject[item.id] = item;
                        }
                    }
                }
            }
        });
    }


    return {
        ...PostUpdateData,
        completeDataObject,
        completeData,
       // postdataStore: postData.slice(2),
        postData: postData,
        filteredDataObject: filteredDataObject,
        tagfilteredDataObject: tagfilteredDataObject,
        preRender: PostUpdateData.preRender,
        freeAdsPosition,
    };

}