import React, { useEffect, useRef, useCallback, useState } from 'react';
import { updateIntraction } from '../../../actions/themeActions';
import { getPersonalize, getPreview } from '../../../utils';

const getEmoji = (type, height) => {
  if (type == 'love') {
    return `<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" height="${height}" width="${height}" fill="#f23543"><path d="M47.6 300.4L228.3 469.1c7.5 7 17.4 10.9 27.7 10.9s20.2-3.9 27.7-10.9L464.4 300.4c30.4-28.3 47.6-68 47.6-109.5v-5.8c0-69.9-50.5-129.5-119.4-141C347 36.5 300.6 51.4 268 84L256 96 244 84c-32.6-32.6-79-47.5-124.6-39.9C50.5 55.6 0 115.2 0 185.1v5.8c0 41.5 17.2 81.2 47.6 109.5z"/></svg>`;
  }
}

let timeout = null

const InteractiveEmoji = ({ isCard, postId, intractiveData, userDetail, wallId, interactiveCount, isDisplay, interactive, interactiveStatus }) => {
  const overlayRef = useRef();
  const cardId = isCard ? `intractive${postId}` : isCard == null ? `popInter${postId}` : 'fire-emoji';
  const [likeStatus, setLikeStatus] = useState(true)

  const handleRemoveFlyingEmoji = useCallback((node) => {
    if (!overlayRef.current) return
    overlayRef.current.removeChild(node);
  }, [])

  const handleDisplayFlyingEmoji =  useCallback((event, emoji) => {
    event.stopPropagation();
    if (!overlayRef.current) return;

    const isClickAllow = isCard ? true : isCard == null ? true : false;
    const data = JSON.parse(localStorage.getItem(`intractivelike${wallId.id}`)) || [];
    const checkLike = data.filter((val) => val.cardId == postId).length == 0;

    if (!isDisplay && isClickAllow) {
      setLikeStatus(false);
      const payload = {
        "postId": postId,
        "action": checkLike ? "prepand" : "remove",
        "wallId": wallId.id,
        "table_name": userDetail.db_table
      }


     if(!getPersonalize()) updateIntraction(payload);

     setLikeStatus(true);
      const res = document.querySelectorAll(`#${cardId}`);
      const htmlContent = checkLike ? `<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" height="22px" width="22px" fill="#f7535f"><path d="M47.6 300.4L228.3 469.1c7.5 7 17.4 10.9 27.7 10.9s20.2-3.9 27.7-10.9L464.4 300.4c30.4-28.3 47.6-68 47.6-109.5v-5.8c0-69.9-50.5-129.5-119.4-141C347 36.5 300.6 51.4 268 84L256 96 244 84c-32.6-32.6-79-47.5-124.6-39.9C50.5 55.6 0 115.2 0 185.1v5.8c0 41.5 17.2 81.2 47.6 109.5z"/></svg>` : `<span class='outline'><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" width='22px' height='22px' fill='#fff'><path d="M225.8 468.2l-2.5-2.3L48.1 303.2C17.4 274.7 0 234.7 0 192.8v-3.3c0-70.4 50-130.8 119.2-144C158.6 37.9 198.9 47 231 69.6c9 6.4 17.4 13.8 25 22.3c4.2-4.8 8.7-9.2 13.5-13.3c3.7-3.2 7.5-6.2 11.5-9c0 0 0 0 0 0C313.1 47 353.4 37.9 392.8 45.4C462 58.6 512 119.1 512 189.5v3.3c0 41.9-17.4 81.9-48.1 110.4L288.7 465.9l-2.5 2.3c-8.2 7.6-19 11.9-30.2 11.9s-22-4.2-30.2-11.9zM239.1 145c-.4-.3-.7-.7-1-1.1l-17.8-20c0 0-.1-.1-.1-.1c0 0 0 0 0 0c-23.1-25.9-58-37.7-92-31.2C81.6 101.5 48 142.1 48 189.5v3.3c0 28.5 11.9 55.8 32.8 75.2L256 430.7 431.2 268c20.9-19.4 32.8-46.7 32.8-75.2v-3.3c0-47.3-33.6-88-80.1-96.9c-34-6.5-69 5.4-92 31.2c0 0 0 0-.1 .1s0 0-.1 .1l-17.8 20c-.3 .4-.7 .7-1 1.1c-4.5 4.5-10.6 7-16.9 7s-12.4-2.5-16.9-7z" /></svg></span>`;
      res.forEach(item => item.innerHTML = htmlContent);
      if (isCard == null) {
        const cardEle = document.getElementById(`intractive${postId}`);
        cardEle.innerHTML = htmlContent;
      }

      localStorage.setItem(`intractivelike${wallId.id}`, JSON.stringify(checkLike ? [...data, { count: interactiveCount + 1, cardId: postId, wallId: wallId.id }] : data.filter((val) => val.cardId != postId)));
    }

    const node = document.createElement('div');
    const emo = document.createElement("span");
    const likeButton = document.getElementById('likebtn');
    const emojiLength = isCard == false ? document.getElementsByClassName('flying-emojis')[0].childNodes.length : false;

    if(isDisplay && isCard == false){
      clearTimeout(timeout)
      likeButton.style.display = 'block';
      timeout = setTimeout(() => {likeButton.style.display = 'none'}, 1000)
    }
    if (checkLike) {
      emo.innerHTML = getEmoji(emoji, isCard ? '26px' : isCard == null ? '26px' : '40px');
      node.appendChild(emo);
      node.className = `emoji ${isCard ? 'wiggle-1' : isCard == null ? 'wiggle-1' : `${emojiLength % 2 == 0 ? 'emoji' : 'emoji2'} vapour-wiggle`}`;
      //node.style.transform = `rotate(${-30 + Math.random() * 60}deg)`
      overlayRef.current.appendChild(node)
    }

    node.addEventListener('animationend', (e) => handleRemoveFlyingEmoji(e.target))
  }, [handleRemoveFlyingEmoji])

  // Remove all event listeners on unmount to prevent console warnings
  useEffect(() => {
    return () => {
      if (overlayRef.current) {
        overlayRef.current?.childNodes.forEach((n) => {
          n.removeEventListener('animationend', handleRemoveFlyingEmoji)
        })
      }
    }
  }, []);

  useEffect(() => {
    if(interactive && window.location.href.includes('personalize') && isCard == false && interactiveStatus) {
      const emojiEle = document.getElementById(`fire-emoji`);
      const likeButton = document.getElementById('likebtn');
      likeButton.style.display = 'block';
      let rep = 1;
      while(rep < 3){
        setTimeout(()=>{
          for(let i=0; i<6; i++) setTimeout(()=>{ emojiEle.click()}, i * 200)
        }, rep * 500);
        rep++;
      }
      if(rep == 3) setTimeout(() => {likeButton.style.display = 'none'}, 6000)
    } 
  }, [interactive])

  return (
    <div className='interactive-emoji' style={{ userSelect: 'none'}}>
      <div className='fire-emoji' id={cardId} style={{ display: isCard ? 'flex' : isCard == null ? 'flex' : 'block', cursor: isDisplay ? 'not-allowed' : 'pointer', pointerEvents: isDisplay ? 'none' : 'auto' }} onClick={(e) =>  likeStatus ? handleDisplayFlyingEmoji(e, 'love') : e.stopPropagation()}>
        {(isDisplay && interactiveCount != 0) || (!isDisplay && intractiveData && intractiveData.length > 0 && JSON.parse(intractiveData).filter((val) => val.cardId == postId).length > 0) ?
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" height="22px" width="22px" fill="#f7535f"><path d="M47.6 300.4L228.3 469.1c7.5 7 17.4 10.9 27.7 10.9s20.2-3.9 27.7-10.9L464.4 300.4c30.4-28.3 47.6-68 47.6-109.5v-5.8c0-69.9-50.5-129.5-119.4-141C347 36.5 300.6 51.4 268 84L256 96 244 84c-32.6-32.6-79-47.5-124.6-39.9C50.5 55.6 0 115.2 0 185.1v5.8c0 41.5 17.2 81.2 47.6 109.5z" /></svg>
          : isCard == false && !getPreview() && !window.location.href.includes("editor=1") ?
            <span className='like-button' id='likebtn' style={{display: 'none'}}><svg width="80" height="80" viewBox="0 0 187 200" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M0 30C0 13.4315 13.4315 0 30 0H157C173.569 0 187 13.4315 187 30V118C187 134.569 173.569 148 157 148H95L41 194.5V148H30C13.4315 148 0 134.569 0 118V30Z" fill="#EC4F5D"/>
            <path d="M112.374 39.25C124.778 39.25 134.833 49.4583 134.833 63.75C134.833 92.3333 104.208 108.667 93.9994 114.792C83.791 108.667 53.166 92.3333 53.166 63.75C53.166 49.4583 63.3743 39.25 75.6243 39.25C83.2192 39.25 89.916 43.3333 93.9994 47.4167C98.0827 43.3333 104.779 39.25 112.374 39.25Z" fill="white"/>
            </svg>
            </span>
            : (!getPreview() && !window.location.href.includes("editor=1")) &&
            <span className='outline'><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" width='22px' height='22px' fill='#fff'><path d="M225.8 468.2l-2.5-2.3L48.1 303.2C17.4 274.7 0 234.7 0 192.8v-3.3c0-70.4 50-130.8 119.2-144C158.6 37.9 198.9 47 231 69.6c9 6.4 17.4 13.8 25 22.3c4.2-4.8 8.7-9.2 13.5-13.3c3.7-3.2 7.5-6.2 11.5-9c0 0 0 0 0 0C313.1 47 353.4 37.9 392.8 45.4C462 58.6 512 119.1 512 189.5v3.3c0 41.9-17.4 81.9-48.1 110.4L288.7 465.9l-2.5 2.3c-8.2 7.6-19 11.9-30.2 11.9s-22-4.2-30.2-11.9zM239.1 145c-.4-.3-.7-.7-1-1.1l-17.8-20c0 0-.1-.1-.1-.1c0 0 0 0 0 0c-23.1-25.9-58-37.7-92-31.2C81.6 101.5 48 142.1 48 189.5v3.3c0 28.5 11.9 55.8 32.8 75.2L256 430.7 431.2 268c20.9-19.4 32.8-46.7 32.8-75.2v-3.3c0-47.3-33.6-88-80.1-96.9c-34-6.5-69 5.4-92 31.2c0 0 0 0-.1 .1s0 0-.1 .1l-17.8 20c-.3 .4-.7 .7-1 1.1c-4.5 4.5-10.6 7-16.9 7s-12.4-2.5-16.9-7z" /></svg></span>}
      </div>

      <div className={`flying-emojis ${isCard == false ? 'reaction-emoji' : ''}`} ref={overlayRef}></div>
    </div>
  )
}

export default InteractiveEmoji;