import React, { Component, Suspense } from 'react';
import { componentWillAppendToBody } from "react-append-to-body";
import { connect } from 'react-redux';
import { themeData } from './actions/themeActions';
import { BrandingUTM, CloudUrl } from './constants';
import { brandingUrl, checkMobileView, getPreview, isAppleSafari, isEditor, onWindowEvent } from './utils';
import InteractiveEmoji from './app/Themes/Elements/InteractiveEmoji';
import { updateProduct } from './actions/displayThemeAction';
import { UPDATE_PRODUCT } from './reducers/reducersKeys';

const OnsiteBanner = React.lazy(() => import('./app/Themes/onsiteBanner'));
const Studio = React.lazy(() => import('./app/Studio'));
const App = React.lazy(() => import('./app'));
const ErrorPage = React.lazy(() => import('./app/Components/ErrorPages'));
const Common = React.lazy(() => import('./app/Components/Common'));
const AppHead = React.lazy(() => import('./app/Components/Head/Head'));
const UserComponents = React.lazy(() => import('./app/Components/UserComponents'));
const SharePost = React.lazy(() => import('./app/Components/UserComponents/SharePost'));
const PostPopUp = React.lazy(() => import('./app/Components/PostPopUp'));
const ReportMedia = React.lazy(() => import('./app/Components/UserComponents/ReportMedia'));
const CustomBranding = React.lazy(() => import('./app/Components/CustomBranding'));
const DisplayPopUp = React.lazy(() => import("./app/Components/PostPopUp/DisplayPopUp")); 
const TaggboxBranding = React.lazy(() => import("./app/Components/TaggboxBranding")); 

const channel = isEditor() ? isAppleSafari() ? window : new BroadcastChannel('connect') : null;

export const addScript = url => {
  const script = document.createElement("script");
  script.src = url;
  script.defer = true;
  document.head.appendChild(script);
}

export const onLoad = () => {
  if (!window.jQuery) addScript(`${CloudUrl}/js/jquery.min.js`);
  addScript(`${CloudUrl}/js/slackdown.js`);
  window.onload = () => {
    if (!window.jQuery) addScript(`${CloudUrl}/js/jquery.min.js`);
    addScript(`${CloudUrl}/js/slackdown.js`);
  }
}

const MyComponent = ({ children }) => { return children; }
const TaggboxThemes = (props) => {
  if (props.loader && props.loader.loader != null && props.error.errorWithMessage == null && !props.error.planLimit && (props.error.themeError == null || (Object.keys(props.error.themeError).length > 0 && props.error.themeError.error_code == 0) || props.wall?.Personalization.demoWall === 1))
    return <Suspense fallback={<></>}><App  {...props} /></Suspense>
  else if (props.error.errorWithMessage || props.error.planLimit || props.error.themeError)
    return <Suspense fallback={<></>}><ErrorPage {...props} /></Suspense>
  else
    return null
}
const AppendedView = componentWillAppendToBody(MyComponent);

const BackLinks = ({ appData }) => {
  let brandNet = appData.webFilters != '' && appData.webFilters ? appData.webFilters.map(({ networkId }) => networkId) : null;
  return <a href={brandingUrl(brandNet, appData.wallID)[0] + BrandingUTM} target="_blank" style={{ display: 'none' }}
    aria-label={brandingUrl(brandNet, appData.wallID)[1]}>{brandingUrl(brandNet, appData.wallID)[1]}</a>
}
class WidgetApp extends Component {
  state = {
    errorData: null, 
    showBranding: false,
  }
  

  componentDidMount() {

    setTimeout(() => onLoad(), 200);

    const { appData } = this.props;

      if(appData.wall.isDisplay && window.location.href.includes('editor=1')){
        const error = appData.error.themeError;
        if(appData.wall.Personalization.demoWall && error.error_code){
            const errorCode = {"error_code": error.error_code,  "older_post": error.older_post,  "older_days": error.older_days}
            onWindowEvent(4,errorCode)    
        }
    } 
    if(channel) channel.addEventListener("message", this.fetchData);
  }

  fetchData = (event) => this.props.themeData(event);
  
  componentDidUpdate(prevProps, prevState) {

    if(this.props.appData.wall.product !== prevProps.appData.wall.product) this.props.updateProduct(this.props.appData.wall.product ? false : true)
    
    let demoWall = prevProps.appData.wall?.Personalization?.demoWall
    if (prevProps.appData.error?.themeError !== prevState.errorData) {
      this.setState({ errorData: prevProps.appData.error?.themeError })
      if (typeof window.onSetErrorCode === 'function' && this.state.errorData !== null) window.onSetErrorCode(this.state.errorData, demoWall)
    }
  }

  dismissPopUp = event => {
    const { showBranding } = this.state;
    this.setState({ showBranding: !showBranding })
  }


  componentWillUnmount(){
    if(channel) channel.removeEventListener("message", this.fetchData); 
  }

  render() {

    const { modalPop, appData } = this.props;
    const { wall } = appData;
    const isBranding = appData && appData.wall && Object.keys(appData.wall).length > 0 && wall.UserDetail && Object.keys(wall.UserDetail).length > 0 ? true : false;

 
    return <>
    
      { wall ? <Common appData={appData} /> : null }

      { modalPop.onSiteIsShowPopUp ? <AppendedView> <Suspense fallback={<></>}><UserComponents /></Suspense></AppendedView> : 
       wall && (!wall.isEditor || getPreview())&& wall.isDisplay && wall.Personalization.is_studio_enabled && wall.UserRule.studio ?
          <Suspense fallback={<></>}>
             <Studio wall={wall}/>
          </Suspense>   
        :
      <>
      { appData && wall && Object.keys(wall).length > 0 ? <Suspense fallback={<></>}>  <AppHead wall={wall} /></Suspense> : null}

      { wall && wall.isDisplay && (wall.UserRule.on_site_upload && wall.UgcSettings.onsite_status && wall.UgcSettings.onsite_button_status)  ? <Suspense fallback={<></>}> <OnsiteBanner wall={wall} /></Suspense> : null }

      <TaggboxThemes {...appData} />
      { wall &&  !wall.isDisplay && isBranding && wall.UserDetail.planId.includes('53') ? <Suspense fallback={<></>}><CustomBranding appData={appData} /> </Suspense> : null}

      {wall &&  wall.Personalization.signageTheme === 51 ? '' : wall &&  wall.isDisplay ? <TaggboxBranding showBranding={this.state.showBranding} wall={wall} dismissPopUp={this.dismissPopUp} /> : null}

       {wall && wall.isDisplay && (!wall.isEditor || window.location.href.includes("studio=1")) &&  wall.ThemeRule.postFeatured && checkMobileView() != 1 &&
          ![74, 75, 76, 77].includes(wall.ThemeRule.themeId) ? (
          <DisplayPopUp wall={wall} isShowPopUp={modalPop.isShowPopUp} data={modalPop.data} languageSetting={appData.languageSetting} networkData={appData.networkData} itemData={appData.postData} brandingNetworks={appData.webFilters != '' && appData.webFilters ? appData.webFilters.map(({ networkId }) => networkId) : null} key={modalPop.isShowPopUp} />
        ) : null}  
 
      {wall &&  !wall.isDisplay && (modalPop.isShowPopUp) ? <AppendedView> <Suspense fallback={<></>}><PostPopUp wall={wall} data={modalPop.data} languageSetting={appData.languageSetting} networkData={appData.networkData} postData={appData.postData} brandingNetworks={appData.webFilters != '' && appData.webFilters ? appData.webFilters.map(({ networkId }) => networkId) : null} /></Suspense> </AppendedView> : null}

      {modalPop.reportStatus ? <AppendedView> <Suspense fallback={<></>}><ReportMedia item={modalPop.reportData} wall={wall} /></Suspense> </AppendedView> : null}


      {modalPop.shareStatus ? <AppendedView> <Suspense fallback={<></>}><SharePost postLink={modalPop.shareData.link} userName={modalPop.shareData.author.name} /></Suspense> </AppendedView> : null}

      {isBranding && !wall.UserDetail.planId.includes('53') ? <AppendedView><BackLinks appData={appData} /></AppendedView> : null}
      {wall.isDisplay ? <div className='interactive-animation'>{wall.Personalization.interactive ? <InteractiveEmoji {...{isCard:false, postId:0, intractiveData:[], userDetail: wall.UserDetail, wallId: wall.Wall, interactiveCount: 0, isDisplay: wall.isDisplay, interactive:wall.Personalization.interactive, interactiveStatus : wall.Personalization.interactiveStatus}} /> : null}</div> : null}
      </>
    }
    
  </>

    }
}

const mapStateToProps = state => {  
  return {
    appData: state.appData,
    modalPop: state.modalPop
  }
}
const mapDispatchToProps = dispatch => {
  return { 
    updateProduct : (data) =>  dispatch({ type: UPDATE_PRODUCT, payload: data }),
    themeData : (data) => dispatch(themeData(data))
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(WidgetApp);