import React, { Component, Suspense } from "react";
import { connect } from "react-redux";
import { themeByWallID } from "./actions/themeActions";
import { displayThemeByWallID } from "./actions/displayThemeAction";
import { getWallID, isDisplayActive } from "./utils";
import WidgetApp from "./widgetApp";

class Main extends Component {
  constructor(props) {
    super(props);
    let wallID = getWallID();
    if (wallID && String(wallID).length > 0 && String(wallID)[0] == "0")
      wallID = String(wallID).slice(1);
    this.state = {  wall_id: wallID };
    isDisplayActive()
      ? props.displayThemeByWallID(wallID)
      : props.themeByWallID(wallID);
  }
  render() {
    const { wall_id } = this.state;
    const { appData } = this.props;

    return (
      <Suspense fallback={<></>}>
        {appData && appData.wall && Object.keys(appData.wall).length > 0 ? <WidgetApp wallID={wall_id} /> : null}
      </Suspense>
    )
  }
}

const mapStateToProps = (state) => {  
  return {
    appData: state.appData,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      themeByWallID: (Id, heightEvent) =>
      dispatch(themeByWallID(Id, heightEvent)),
      displayThemeByWallID: (Id, heightEvent) =>
      dispatch(displayThemeByWallID(Id, heightEvent)),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(Main);